import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Meta } from '../models/meta';
import { Events } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
/**
 * Service para controlar a questão financeira do paciente
 */
export class FinanceiroService {

  private financeiroDB:firebase.firestore.CollectionReference;
  private usuario: firebase.User;

  constructor(private events:Events) { 
    firebase.auth().onAuthStateChanged(user => this.usuario = user);
    this.financeiroDB = firebase.firestore().collection('metas-financeiras');
  }
  /**
   * Busca todas as metas de um paciente
   */
  async buscarTodos(): Promise<Meta[]> {
    let snapshots = await this.financeiroDB.where('pacienteUID', '==', this.usuario.uid).get();
    const metas:Meta[] = []
    snapshots.forEach(doc => {
      metas.push(Object.assign(new Meta, doc.data()))
    })
    return metas;
  } 

  /**
   * Cadastra uma nova meta
   * @param meta 
   */
  cadastrar(meta:Meta): Promise<any> {
    const doc = this.financeiroDB.doc();
    meta.pacienteUID = this.usuario.uid;
    meta.uid = doc.id;
    return doc.set(Object.assign({}, meta));
  }

  /**
   * Atualiza uma meta
   * @param meta 
   */
  atualizar(meta:Meta): Promise<any> {
    return this.financeiroDB.doc(meta.uid).set(Object.assign({}, meta));
  }

  /**
   * Remove uma meta
   * @param meta 
   */
  excluir(meta:Meta): Promise<any> {
    return this.financeiroDB.doc(meta.uid).delete();
  }
}
