import { Component, OnInit } from '@angular/core';
import { ToastController, AlertController } from '@ionic/angular';
import { FinanceiroService } from 'src/app/services/financeiro.service';
import { PacienteService } from 'src/app/services/paciente.service';
import { Paciente } from 'src/app/models/paciente';
import { Meta } from 'src/app/models/meta';

@Component({
  selector: 'app-financeiro',
  templateUrl: './financeiro.page.html',
  styleUrls: ['./financeiro.page.scss'],
})
/**
 * Pagina do Paciente que exibe suas conquistas financeiras
 */
export class FinanceiroPage implements OnInit {

  paciente = new Paciente;
  metas:Meta[] = [];

  constructor(public toastController: ToastController, public alertController: AlertController, 
              private pacienteService:PacienteService, private financeiroService:FinanceiroService) { }

  async ngOnInit() {
    this.paciente = await this.pacienteService.getPacienteLogado();
    this.atualizar();
  }

  /** Usa uma meta 
   * O dinheiro da meta será discontato
  */
  usar(meta:Meta) {
    //Lembrar de salvar quantas metas já foram concluidas para conquista
    this.alertController.create({
      header: 'Usar meta',
      message: 'Dejesa realmente usar essa meta?',
      buttons: [
        'Cancelar',
        {text: 'Usar',  handler: async () => {
          meta.usado = true;
          await this.financeiroService.atualizar(meta);
          this.atualizar();
          
          //Baixa o valor disponivel 
          this.paciente.dinheiroDisponivel -= meta.objetivo;
          this.pacienteService.atualizarPaciente(this.paciente)

          this.toastController.create({
            message: 'Meta usada!',
            duration: 3000,
            position: 'bottom'
          }).then(t => t.present());
        }
      
      }]
    }).then(alert => alert.present());
  }

  /** Remover uma meta */
  remover(meta:Meta) {

    this.alertController.create({
      header: 'Remover meta',
      message: 'Dejesa realmente remover essa meta?',
      buttons: [
        'Cancelar',
        {text: 'Remover',  handler: async () => {

          await this.financeiroService.excluir(meta);
          this.atualizar();

          this.toastController.create({
            message: 'Removida com sucesso',
            duration: 3000,
            position: 'bottom'
          }).then(t => t.present());
        }


      }]
    }).then(alert => alert.present());

  
  }

  /**
   * Adiciona uma nova meta
   */
  async adicionar() {

    this.alertController.create({
      header: 'Adicionar nova meta',
      inputs: [
        {name: 'titulo', placeholder: 'Digite um nome para a meta', type: 'text'},
        {name: 'objetivo', placeholder: 'Digite o valor a juntar', type: 'number'},
      ],
      buttons: [
        {text:'Cancelar', cssClass:'btn-cancelar'},
        {text: 'Adicionar', cssClass:'btn-ok',   handler: async (dados) => {
          const meta = new Meta(dados.titulo, Number(dados.objetivo));
          console.log(meta);
          await this.financeiroService.cadastrar(meta);
          this.atualizar();

          this.toastController.create({
            message: 'Meta adicionada',
            duration: 3000,
            position: 'bottom'
          }).then(t => t.present());
        }
      
      }]
    }).then(alert => alert.present());
  }

  /** Busca as metas do paciente */
  async atualizar() {
    this.metas = await this.financeiroService.buscarTodos();
  }
}
